.elements {
  max-width: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(282px, 1fr));
  gap: 20px 17px;
  padding-bottom: 67px;
  min-height: 55vh;
}

@media screen and (max-width: 420px) {
  .elements {
    padding-bottom: 50px;
    gap: 20px;
  }
}